.board-svg-container {
  flex: 1 1 0;
  container: board-svg-container-layout / size;
  /* Set position to relative so this container becomes the SVG's containing block. */
  position: relative;
}

.board-svg {
  aspect-ratio: 1 / 1;

  /* Occupy the entire width if width <= height. */
  width: 100%;
  height: auto;

  /* Center the SVG horizontally and vertically. */
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

/* Occupy the entire height if width > height. */
@container board-svg-container-layout (aspect-ratio > 1) {
  .board-svg {
    width: auto;
    height: 100%;
  }
}
